import type { AxiosRequestConfig } from 'axios';
import type { ContextAPI } from './types';
import type {
  NoraAPIGetSubscriptionsPlansRequest, NoraAPIGetSubscriptionsPlansResponse,
  NoraAPIGetSubscriptionsRequest,
  NoraAPIGetSubscriptionsResponse
} from './types/subscriptions.types';

export class SubscriptionsAPI {
  readonly path = '/subscriptions';
  readonly context: ContextAPI;

  constructor(ctx: ContextAPI) {
    this.context = ctx;
  }

  /**
   * Get info about available subscriptions
   * @param data SubscriptionsAPIGetRequest
   * @param config AxiosConfig
   * @returns List of available subscriptions
   */
  get(data?: NoraAPIGetSubscriptionsRequest, config?: AxiosRequestConfig): Promise<NoraAPIGetSubscriptionsResponse> {
    return this.context.post<NoraAPIGetSubscriptionsResponse, NoraAPIGetSubscriptionsRequest>(
      `${this.path}/get`,
      data || {},
      config
    );
  }

  /**
   * Get all available subscriptions with addons combined by subscription plans.
   * @param data
   * @param config
   */
  getPlans(
    data?: NoraAPIGetSubscriptionsPlansRequest,
    config?: AxiosRequestConfig
  ): Promise<NoraAPIGetSubscriptionsPlansResponse> {
    return this.context.post<NoraAPIGetSubscriptionsPlansResponse, NoraAPIGetSubscriptionsPlansRequest>(
      `${this.path}/plans/get`,
      data || {},
      config
    );
  }
}
